import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import Typography from '@material-ui/core/Typography';
import { withStyles } from '@material-ui/core/styles';
import Header from '../Header/';
import Footer from '../Footer/';
import * as colors from '../../constants/colors';
import PhotoGrid from './teamPhotos';

const Team = ({classes}) => {
  
  return (
    <div className={classes.container}>
      <Header fixed={true}/>
      <div className={classes.teamsContainer}>
        <header className={classes.header}>
            <Typography className={classes.h1} variant={'h1'}>Quines Somos?</Typography>	
        </header>
        <div className="container text-justify">      
          <section className={classes.section}>
          <div>
            <Typography align = {"justify"} className={classes.textclass}>
              La plataforma <a href="https://www.mexicovid19.mx">www.mexicovid19.mx </a> 
              es un esfuerzo conjunto de un grupo de alumnos y profesores del Tecnolgico de 
              Monterrey para dar seguimiento y fcil acceso a la informacin de salud ms relevante 
              en el contexto de la pandemia de Coronavirus en Mxico. Mediante herramientas de 
              visualizacin e investigacin aplicada, esta plataforma busca ser un nodo para discusin 
              y el anlisis que reflejen la importancia de los datos para la toma de decisiones y el 
              diseo de poltica pblica. Este proyecto surgi como un proyecto de la clase Herramientas 
              Tecnolgicas para las Ciencias Sociales, un bloque del modelo Tec 21. Los estudiantes son de 
              diversas carreras y participan voluntariamente con su tiempo en la recoleccin de informacin, 
              ejecucin de las visualizaciones de informacin cuantitativa y desarrollo de la plataforma.
            </Typography>  
          </div>
          </section>
          <PhotoGrid/>
        </div>   
      </div>
      <Footer/>
    </div>
    );
}

const styles = () => ({
  section: {
		margin: '20px 0px',
    borderRadius: '5px',
    padding: '20px',
    backgroundColor: colors.GRAY,
	},
  
  container: {
    width: '100%',
    backgroundColor: colors.GRAY,
  },

  teamsContainer: {
    width: '70%',
		margin: 'auto',
		padding: '25px',
    paddingTop: '128px',
    backgroundColor: colors.WHITE
  },
  
  header: {
		borderBottom: `1px solid ${colors.BLACK}`,
		display: 'flex',
    alignItems: 'baseline',
    justifyContent: 'space-between',
  },

  h1: {
		fontSize: '36px'
	},
  
  textclass: {
    marginTop: '0px',
    marginBottom: '0px'
  }, 
  
});
 
export default withStyles(styles)(Team);