const styles = () => ({
    container: {
        width: '100%',
        backgroundColor: colors.GRAY,
    },

    photoGrid: {
        width: '900px',
        display: 'flex',
        justifyContent: 'flex-start',
    },

    photoGridItem: {
        border: '1px solid #fff',
        width: '300px',
        height: '300px',
        marginTop: '30px !important',
        marginBottom: '150px !important'
    },

    photoGridItemTeacher: {
        border: '1px solid #fff',
        width: '300px',
        height: '300px',
        marginTop: '30px !important',
        marginBottom: '80px !important'
    },

    contactText:{
        display:'flex',
        
    },

    iconspace: {
        marginRight: '5px',
        top: '0px',
        left: '0px',
        cursor: 'pointer',
        color: colors.BLACK,
    },
});

   
export default withStyles(styles)(PhotoGrid);